import React from 'react'
import Link from 'gatsby-link'
import District from '../../components/District'

const PhuNhuan = ({ data }) => {
  return <District nodes={data.allLocationsXlsxSheet1.edges} />
}

export default PhuNhuan

export const PhuNhuanQuery = graphql`
  query PhuNhuanQuery {
    allLocationsXlsxSheet1(filter: { district: { eq: "Phú Nhuận" } }) {
      edges {
        node {
          plf_id
          complete_address
          longitude
          latitude
          city
          ward
          district
          planned_launch_date
          week
          status
          contact_email
          notes
        }
      }
    }
  }
`



